	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Miguel Grinberg\n\nWriting a fully complaint REST API is hard, so hard it is too common for APIs to violate one or more of the REST architectural principles. In this talk I will describe the six REST principles, and I will tell you what happens if you don't follow them.\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT45M31S"},"presenter":{"name":null},"slug":"miguel-grinberg-is-your-rest-api-restful-pycon-2015","source":{"id":"pZYRC8IbCwk","provider":"youtube"},"tag":[],"title":"Miguel Grinberg - Is Your REST API RESTful? - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"miguel-grinberg-is-your-rest-api-restful-pycon-2015","tags":[]}}

